import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';
import { Text, Localizer, withText } from 'preact-i18n';
import PubSub, { topics } from '../../lib/pubsub';

import Teams from '../../components/team/teams';
import CalendarPlan from '../../components/workout/calendarPlan';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

@observer
class InputNew extends Component {
    onInput = (e) => {
        const { field } = this.props;
        const value = e.target.value;
        const { workoutPlanStore } = this.props.stores;
        // console.log('trackStore.newPlan', trackStore.newPlan);
        workoutPlanStore.updateObjectKeyValue('newPlan', field, value);
    }

    render() {
        const { field, icon, title, help, type, datalist = [] } = this.props;
        const { workoutPlanStore } = this.props.stores;
        const { newPlan } = workoutPlanStore;

		if (type === 'select') {
            return (
                <div class={`form-group  mb-5 pb-1 border-bottom`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>

                    <select
                        list={`list-${field}`}
                        class={`form-control`}
                        id={`${field}Input`}
                        aria-describedby={`${field}Help`}
                        value={newPlan[field]}
                        data-field={field}
                        onInput={this.onInput}
                        autocomplete='off'
                    >
                        <option value=''>-- <Text id='workout.plan.choose-team'>Velg team</Text> --</option>
                        {datalist.map(el => (
                            <option value={el[0]}>{el[1]}</option>
                        ))}
                    </select>
					<small id={`${field}Help`} class='form-text text-muted'>{help}</small>
                </div>
            );
        }

        return (
            <div class='form-group'>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <input type='text' class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    onInput={this.onInput}
                    value={newPlan[field]}
                />
                <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
            </div>
        );
    }
}

const initialState = {
	isLoading: false,
	showDuplicate: false,
};

@observer
class WorkoutPlans extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadAll = (props = this.props) => {
		this.loadWorkoutPlans(props);
		this.loadWorkoutSummary(props);
	}

	async loadWorkoutSummary(props = this.props) {
        this.setState({ isLoading: true });
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;

        const currentMonth = new Date().getMonth() + 1;
        const { month = currentMonth, year = util.getYear(), team = currentTeam, dog = {} } = props;
        await userStore.getWorkoutSummary({ team, dog: dog.id, daysBack: 500 });
        await userStore.getWorkoutSummaryDay({ daysBack: 500, team, dog: dog.id });
        this.setState({ isLoading: false });
    }

	loadWorkoutPlans = async (props = this.props) => {
		this.setState({ isLoading: true });
		const { year } = props;

		const currentMonth = new Date().getMonth() + 1;
        const currentYear = new Date().getFullYear();
		const seasonStart = parseInt(year || (currentMonth < 8 ? currentYear - 1 : currentYear), 10);
		const { workoutPlanStore, workoutStore } = props.stores;
		const { currentTeam } = workoutStore;

		workoutPlanStore.updateItem('workoutPlans', []);
		workoutPlanStore.updateItem('workoutPlan', {});

		await workoutPlanStore.load({ 
			query: {
				team: currentTeam,
				seasonYear: seasonStart,
				seasonMonth: 6,
			},
			addData: ['tips'],
		});

		const { workoutPlans } = workoutPlanStore;
		const workoutPlanCurrent = workoutPlans[0] || {};
		if (workoutPlanCurrent.id) {
			await workoutPlanStore.load(workoutPlanCurrent.id);
			workoutPlanStore.prepareWeekGraphs({ year });
		} else {
			workoutPlanStore.updateKeyValue('workoutPlan', {});
			workoutPlanStore.updateKeyValue('workoutPlans', []);
			workoutPlanStore.resetGraphPlanWeekDistanceCurrent();
		}
		this.setState({ isLoading: false });
	}

	toggleDuplicate = () => {
		const { showDuplicate } = this.state;
		this.setState({ showDuplicate: !showDuplicate });
	}

	duplicateWorkoutPlan = async () => {
        const { workoutPlanStore } = this.props.stores;
		const { newPlan, workoutPlan } = workoutPlanStore;
		const newTeam = util.asNumber(newPlan.team);
		const response = await workoutPlanStore.createWorkoutPlan({
			newPlan: {
				title: newPlan.title,
				seasonMonth: workoutPlan.seasonMonth,
				seasonYear: workoutPlan.seasonYear,
				team: newTeam,
				user: workoutPlan.user,
				images: workoutPlan.images,
				days: workoutPlan.days,
				weeks: workoutPlan.weeks,
				months: workoutPlan.months,
			}
		});
		if (response && response.id) {
			PubSub.publish(topics.STATUS_MESSAGE, {
				message: 'Workoutplan was duplicated successfully',
				type: 'success',
				icon: 'fas fa-check',
			});
			route(`/workouts/plan`);
		}
	}

    componentDidMount() {
		this.loadWorkoutPlans();
		this.loadWorkoutSummary();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.month !== this.props.month
            || nextProps.year !== this.props.year
            || nextProps.team !== this.props.team) {
            this.loadWorkoutPlans(nextProps);
            this.loadWorkoutSummary(nextProps);
        }
    }

	render() {
        const { scrolledDown } = this.props;
		const { isLoading, showDuplicate } = this.state;
		const { userStore, appState, workoutPlanStore, workoutStore } = this.props.stores;
		const { workoutPlan } = workoutPlanStore;
		const { currentTeam } = workoutStore;
		const currentTeamObject = userStore.findTeam(currentTeam, true) || {};
		const { teams } = userStore;
		const { workoutSummaryWeekDay = [] } = userStore;

		const { darkmode } = appState;
		const { message, year } = this.props;
		const {
			isAdmin,
		} = userStore;

		const currentMonth = new Date().getMonth() + 1;
        const currentYear = new Date().getFullYear();
		const seasonStart = parseInt(year || (currentMonth < 8 ? currentYear - 1 : currentYear), 10);

		const nextYear = seasonStart + 1;
		const prevYear = seasonStart - 1;

		const months = util.monthRange(`${seasonStart}-08-01`, `${seasonStart + 1}-07-31`);
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top pt-2' style={`margin-top: ${scrolledDown > 0 ? 0 : SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'}; transition: all 0.3s ease-in-out;`}>
					<div class='col text-center subtopmenu'>
						<a href='/workouts/' class='text-secondary stretched-link'><nobr><i class='fas fa-chart-line' /> <Text id='workout.plan.dashboard'>Oversikt</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/workouts/list' class='text-secondary stretched-link'><nobr><i class='fas fa-list' /> <Text id='workout.plan.list'>Liste</Text></nobr></a>
					</div>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<a href='/workouts/plan' class='text-primary stretched-link'><nobr><i class='fas fa-calendar-alt' /> <Text id='workout.plan.plan'>Plan</Text></nobr></a>
					</div>
				</div>

				{message && (
					<div class='alert alert-success mb-3' role='alert'>
						<i class='fas fa-check' /> {message}
					</div>
				)}
				
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom mb-5'>
						<Teams stores={this.props.stores} callback={this.loadAll} key={`teams-${currentTeam}`} />
					</div>
					{/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>

                        <div class='text-center mt-4 mb-4 text-muted'>
                            <div class='display-1'><i class='fab fa-pagelines' /></div>
    						Treningsplan kommer ganske snart :)
                        </div>
					</div> */}
				</div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-2 text-center'>		
						<a href={`/workouts/plan/${nextYear}/skipScroll`} class='float-right btn btn-sm btn-outline-secondary ml-2'><i class='fas fa-arrow-right' /></a>
						<a href={`/workouts/plan/${prevYear}/skipScroll`} class='float-left btn btn-sm btn-outline-secondary mr-2'><i class='fas fa-arrow-left' /></a>
						<h5><Text id='workout.plan.season'>Sesongen</Text> {seasonStart} - {seasonStart + 1}</h5>
						<small>{workoutPlan.id ? <>{workoutPlan.title}</> : <Text id='workout.plan.no-plan'>Ingen plan</Text>}</small><br />
						<small class='text-muted'><i class='fas fa-users' /> {currentTeamObject.name}</small>
					</div>
				</div>

				{months.map(m => (
					<div>
						<CalendarPlan
							stores={this.props.stores}
							basehref='/workouts/plan/'
							month={m.month}
							year={m.year}
							key={`${workoutPlan.id || seasonStart}-${currentTeam}-${workoutSummaryWeekDay.length}`}
							showNavigation={false}
							currentTeam={currentTeam}
						/>
					</div>
				))}

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-2 mt-5'>
						{showDuplicate ? (
							<>
								<h5><Text id='workout.plan.duplicate'>Legg til ny treningsrunde</Text></h5>
                                <InputNew stores={this.props.stores} field='title' icon='fas fa-heading'
                                    title={<Text id='workout.plan.title'>Tittel</Text>}
                                    help={<Text id='workout.plan.title-help'>Tittel på denne treningplanen.</Text>} />
                                <InputNew stores={this.props.stores} field='team' icon='fas fa-users'
									type='select'
									datalist={teams.filter(e => e.id !== workoutPlan.team).map(e => [e.id, e.name])}
                                    title={<Text id='workout.plan.team'>Team</Text>}
                                    help={<Text id='workout.plan.team-help'>Teamet som ønsker denne treningsplanen.</Text>} />
								<button class='btn btn-secondary' onClick={this.duplicateWorkoutPlan}>
									<Text id='workout.plan.save-new'>Lagre ny treningsplan</Text>
								</button>
							</>
						) : (
							<>
								<button class='btn btn-secondary' onClick={this.toggleDuplicate}>
									<Text id='workout.plan.duplicate'>Duplisere treningsplan</Text>
								</button>
							</>
						)}
					</div>
				</div>
			</div>
		);
	}
}

export default WorkoutPlans;
