import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { Text, Localizer, withText } from 'preact-i18n';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

import mu from '../../lib/musher-util';

const RED_DAYS = [0, 6];

@observer
class Calendar extends Component {
	setFocus = () => {
		if (this.inputRef) {
			this.inputRef.focus();
		}
	}
	editMonth = (e) => {
		const month = e.target.parentElement.dataset.month || e.target.dataset.month;
		const year = e.target.parentElement.dataset.year || e.target.dataset.year;
		const distance = e.target.parentElement.dataset.distance || e.target.dataset.distance;
		// console.log({ month, year });
		this.setState({
			showInput: true,
			inputYear: year,
			inputMonth: month,
			inputDay: null,
			inputWeek: null,
			newDistance: distance || null,
		}, () => this.setFocus());
	}

	editWeek = (e) => {
		const week = e.target.parentElement.dataset.week || e.target.dataset.week;
		const year = e.target.parentElement.dataset.year || e.target.dataset.year;
		const distance = e.target.parentElement.dataset.distance || e.target.dataset.distance;
		// console.log({ week, year });
		this.setState({
			showInput: true,
			inputYear: year,
			inputWeek: week,
			inputMonth: null,
			inputDay: null,
			newDistance: distance || null,
		}, () => this.setFocus());
	}

	editDay = (e) => {
		const day = e.target.parentElement.dataset.day || e.target.dataset.day;
		const month = e.target.parentElement.dataset.month || e.target.dataset.month;
		const year = e.target.parentElement.dataset.year || e.target.dataset.year;
		const week = e.target.parentElement.dataset.week || e.target.dataset.week;
		const distance = e.target.parentElement.dataset.distance || e.target.dataset.distance;
		// console.log({ day, month, year });
		this.setState({
			showInput: true,
			inputYear: year,
			inputMonth: month,
			inputDay: day,
			inputWeek: week,
			newDistance: distance || null,
		}, () => this.setFocus());
	}

	saveDistance = async () => {
		const { workoutPlanStore, workoutStore, userStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		const teamName = userStore.findTeam(currentTeam) || '';
		const { year, month } = this.props;
		const { workoutPlans } = workoutPlanStore;
		const workoutPlanCurrent = workoutPlans[0] || {};
		const { inputYear, inputMonth, inputWeek, inputDay, newDistance } = this.state;
		// console.log({ showInput, inputYear, inputMonth, inputWeek, inputDay, newDistance });

		const currentMonth = month || new Date().getMonth() + 1;
		const currentYear = year || util.getYear();
		const seasonStart = parseInt(currentMonth < 8 ? currentYear - 1 : currentYear, 10);

		if (!workoutPlanCurrent.id) {
			const response = await workoutPlanStore.insert({
				title: `Workout plan for ${seasonStart} - ${seasonStart + 1}: ${teamName}`,
				team: currentTeam,
				seasonYear: seasonStart,
				seasonMonth: 6,
			});
			const { id: newId } = response.data;
			await workoutPlanStore.load(newId);
		} else {
			await workoutPlanStore.load(workoutPlanCurrent.id);
		}
		const { workoutPlan } = workoutPlanStore;
		const { id } = workoutPlan;
		// console.log({ id });

		let field;
		if (inputYear) {
			if (inputDay) {
				field = 'days';
			} else {
				field = 'weeks';
			}
		}
		await workoutPlanStore.saveField(id, field, {
			year: inputYear,
			month: inputMonth,
			day: inputDay,
			week: inputWeek,
			distance: newDistance,
		});
		await workoutPlanStore.load(id);

		this.resetInput(true);
	}

	resetInput = (nextDay) => {
		if (nextDay) {
			const { inputYear, inputMonth, inputDay } = this.state;
			const nextDay = util.addDays(new Date(inputYear, inputMonth - 1, inputDay, 0, 0, 0, 0), 1);
			this.setState({
				showInput: true,
				inputYear: nextDay.getFullYear(),
				inputMonth: nextDay.getMonth() + 1,
				inputDay: nextDay.getDate(),
				inputWeek: util.getWeek(nextDay),
				newDistance: null,
			});
			this.setFocus();
		} else {
			this.setState({
				showInput: false,
				inputYear: null,
				inputMonth: null,
				inputDay: null,
				inputWeek: null,
				newDistance: null,
			});
		}
	}

	checkKeydown = (e) => {
		switch (e.key) {
			case 'Enter':
				this.saveDistance();
				break;

			case 'Escape':
				this.resetInput();
				break;
		
			default:
				break;
		}
	}

	componentDidMount() {
		this.updateWorkoutPlanTips();
	}

	updateWorkoutPlanTips = () => {
		// console.log('updateWorkoutPlanTips');
		const { userStore } = this.props.stores;
		const { user = {} } = userStore;
		const { settings = {} } = user;
		this.setState({ hideWorkoutPlanTips: settings.hideWorkoutPlanTips });
	}

	toggleTips = async () => {
		const { hideWorkoutPlanTips } = this.state;
		const { userStore } = this.props.stores;
		const newValue = hideWorkoutPlanTips === 1 ? 0 : 1;
		this.setState({ hideWorkoutPlanTips: newValue });
		await userStore.updateSetting({ 'settings.hideWorkoutPlanTips': newValue }, { hideWorkoutPlanTips: newValue });
	}

	render() {
		const { hideWorkoutPlanTips } = this.state;
		const { appState, workoutPlanStore, userStore, workoutStore, teamStore } = this.props.stores;
		const { user } = userStore;
		const { language = 'en' } = user;
		const { darkmode } = appState;
		const DAYS = mu.getDays(language);
		const MONTHS = mu.getMonths(language);

		const { showInput, inputYear, inputMonth, inputWeek, inputDay, newDistance } = this.state;

        const currentDay = new Date().getDate();
        const currentMonth = new Date().getMonth() + 1;
        const currentYear = new Date().getFullYear();

		const currentEpoch = new Date().getTime();
		const { month = currentMonth, year = currentYear, dog, basehref, showNavigation = true, showHeader = true, currentTeam } = this.props;

		const seasonStart = parseInt(month < 8 ? year - 1 : year, 10);

        const monthNum = parseInt(month, 10);
        const yearNum = parseInt(year, 10);
        const { month: nextMonth, year: nextYear } = mu.getNextMonth(monthNum, yearNum);
        const { month: prevMonth, year: prevYear } = mu.getPrevMonth(monthNum, yearNum);

        const daysOfMonth = util.getDaysInMonth(month, year);
        const weeksInMonth = util.getWeeksInMonth(month, year);
        const firstWeek = weeksInMonth[0].week;
        const daysOfWeek = util.getDaysInWeek(firstWeek, year, month);
        let totalDistanceKmMonth = 0;
		let totalDistanceKmMonthPlan = 0;
		let totalWorkoutDistanceKmMonth = 0;

		const monthEpoch = daysOfMonth[daysOfMonth.length - 1].getTime();
		const isMonthInTheFuture = monthEpoch > currentEpoch;

		const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(year, monthNum) || {};
		const { workoutPlan, tips } = workoutPlanStore;

		const tipsMonth = tips[`${year}-${util.padDate(month)}`];
		const hasTipsMonth = tipsMonth && (tipsMonth.comments.length > 0 || tipsMonth.distance);

		const team = teamStore.findTeam(currentTeam) || {};

		return (
			<>
                {showHeader && <>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4 text-center'>
						{showNavigation && <a href={`${basehref || '/workouts/month/'}${nextMonth}/${nextYear}/skipScroll`} class='float-right btn btn-sm btn-outline-secondary ml-2'><i class='fas fa-arrow-right' /></a>}
						{showNavigation && <a href={`${basehref || '/workouts/month/'}${prevMonth}/${prevYear}/skipScroll`} class='float-left btn btn-sm btn-outline-secondary mr-2'><i class='fas fa-arrow-left' /></a>}
						<h3><Text id='workout.plan.month'>Måned</Text> - {MONTHS[month - 1]} {year} {summaryMonth.distance ? `- ${util.format(summaryMonth.distance, 0)} km` : ''}</h3>
						<h5 class='text-muted font-weight-light'><Text id='workout.plan.season'>Sesongen</Text> {seasonStart} - {seasonStart + 1}</h5>
						<div>
							{workoutPlan.id ? <>
								<small>{workoutPlan.title}</small>
							</> : <>
								<div class='display-4'>
									<i class='far fa-sad-tear text-muted' />
								</div>
								<h5 class='font-weight-lighter text-muted'>
									<Text id='workout.plan.no-workout-plan-found' fields={{ name: team }}>Ingen plan</Text>
								</h5>
								<div class='text-muted'>
									<Text id='workout.plan.click-to-start'>Click on a date to get started.</Text>
								</div>
							</>}
						</div>

						{dog && <div>{dog.name}</div>}
						{/* <small>{util.isoDate(daysOfMonth[0], false, false, true)} - {util.isoDate(daysOfMonth[daysOfMonth.length - 1], false, false, true)}</small> */}
					</div>
				</>}

                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
					{!!(showInput && inputYear && inputWeek && !inputDay) && (
						<>
							<div class='form-inline w-100'>
								<div class='form-group w-75 mr-3 mb-0'>
									<input class='form-control w-100 form-control' type='text' 
										placeholder={`Enter distance for ${inputYear} w${inputWeek}`}
										ref={c => this.inputRef = c}
										onInput={linkState(this, 'newDistance')}
										onKeydown={this.checkKeydown}
										autocomplete='off'
										value={newDistance || ''}
									/>
								</div>
								<button type='button' class='btn btn-primary' onClick={this.saveDistance}><Text id='workout.plan.save'>Lagre</Text></button>
							</div>
						</>
					)}
					{!!(showInput && inputYear && inputMonth) && (
						<>
							<div class='form-inline w-100'>
								<div class='form-group w-75 mr-3 mb-0'>
									<input class='form-control w-100 form-control' type='text'
										placeholder={`Enter distance for ${inputYear}-${util.padDate(inputMonth)} ${inputDay ? `-${util.padDate(inputDay)}` : ''}`}
										ref={c => this.inputRef = c}
										onInput={linkState(this, 'newDistance')}
										onKeydown={this.checkKeydown}
										autocomplete='off'
										value={newDistance || ''}
									/>
								</div>
								<button type='button' class='btn btn-primary' onClick={this.saveDistance}><Text id='workout.plan.save'>Lagre</Text></button>
							</div>
						</>
					)}
				</div>

				{language === 'no' && <>
					{hideWorkoutPlanTips ? <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4 d-flex justify-content-end'>
							<button
								type='button'
								class='btn btn-outline-primary btn-sm'
								onClick={this.toggleTips}
							>
								<i class='fas fa-eye' /> <Text id='workout.plan.show-tips'>Show tips</Text>
							</button>
						</div>
					</> : <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4 d-flex justify-content-end'>
							<button
								type='button'
								class='btn btn-primary btn-sm'
								onClick={this.toggleTips}
							>
								<i class='fas fa-eye-slash' /> <Text id='workout.plan.hide-tips'>Hide tips</Text>
							</button>
						</div>
					</>}
				</>}

                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
					<div class='table-responsive'>
						<table class='table table-striped table-sm'>
							<thead>
								<tr>
									<th class='text-muted'><small><Text id='workout.plan.week'>Uke</Text></small></th>
									{daysOfWeek.map(day => <th><small>{DAYS[day.getDay()]}</small></th>)}
									<th class='text-muted'><small><Text id='workout.plan.sum'>Sum</Text></small></th>
									<th class='text-muted'><small><Text id='workout.plan.planned'>Plan</Text></small></th>
									{/* <th class='text-muted'>Totalt</th> */}
								</tr>
							</thead>
							<tbody>
								{weeksInMonth && weeksInMonth.map(weekObj => {
									const daysOfWeek = util.getDaysInWeek(weekObj.week, year, month);
									const summaryWeek = workoutPlanStore.findWorkoutPlanSummaryWeek(util.asNumber(year), weekObj.week) || {};
									const weekEpoch = daysOfWeek[6].getTime();
									const isWeekInTheFuture = weekEpoch > currentEpoch;
									let totalWeek = 0;
									let totalWorkoutWeek = 0;
									if (summaryWeek.distance > 0) {
										totalDistanceKmMonthPlan += summaryWeek.distance;
									}
									const tipsWeek = tips[`${year}-${util.padDate(month)}-w${util.padDate(weekObj.week)}`];
									const hasTipsWeek = tipsWeek && (tipsWeek.comments.length > 0 || tipsWeek.distance);

									return (<>
										<tr>
											<td class={`text-right text-muted pt-3 border-right ${!inputDay && inputYear == year && inputWeek == weekObj.week ? 'border rounded border-primary' : ''}`}>
												<small onClick={this.editWeek} data-week={weekObj.week} data-year={year} data-distance={summaryWeek.distance}>
													{weekObj.week}
												</small>
											</td>
											{daysOfWeek.map(day => {
												const yyyy = day.getFullYear();
												const mm = day.getMonth() + 1;
												const dd = day.getDate();
												const epoch = day.getTime();
												const isDayInTheFuture = epoch > currentEpoch;
												const summaryWorkout = userStore.findWorkoutSummaryWeekDay((isDayInTheFuture ? yyyy - 1 : yyyy), mm, dd, monthNum) || {};
												const summary = workoutPlanStore.findWorkoutPlanSummaryDay(yyyy, mm, dd, monthNum) || {};
												if (summary && summary.distance) {
													totalWeek += summary.distance;
													totalDistanceKmMonth += summary.distance;
												}
												if (summaryWorkout.distanceKm) {
													totalWorkoutWeek += summaryWorkout.distanceKm;
													totalWorkoutDistanceKmMonth += summaryWorkout.distanceKm;
												}

												const isChosen = (inputYear == yyyy && inputMonth == mm && inputDay == dd);
												const isToday =  (currentYear == yyyy) && (currentMonth == mm) && (currentDay == dd);
												const isDayOff = RED_DAYS.indexOf(day.getDay()) > -1;

												// console.log(summary);
												// count: 1, day: 5, distanceKm: 10, duration: 3600, elevation: 200, month: 9, speedAvg: 12, year: 2020
												return (
													<td class={`text-center overflow-hidden font-weight-light position-relative pt-3 pb-3 border-right ${isChosen ? 'calendarIsChosen' : ''} ${isToday ? 'calendarIsToday' : ''} ${isDayOff ? 'calendarIsWeekend' : ''}`}
														style={`font-size: 0.8em; min-width: 3rem;`}
														onClick={this.editDay} data-day={day.getDate()} data-month={day.getMonth() + 1} data-year={year} data-week={weekObj.week} data-distance={summary.distance}
													>
														<div class='position-absolute text-muted' style='top: 0px; right: 2px; font-size: 0.7em;'>
															{day.getDate()}
														</div>
														{summary && summary.distance ? 
															<div class='rounded-pill w-100 px-1'
																style={`background-color: ${mu.getPlanColor(summary, darkmode)};`}
															>
																<Distance stores={this.props.stores} value={summary.distance} />
																{summary.count > 1 && <span class='position-absolute text-danger' style='bottom: 0; right: 0px; font-size: 0.6em;'>{summary.count}</span>}
															</div> : '-'}
														{summaryWorkout.distanceKm > 0 && (
															<span class={`position-absolute font-weight-bold`}
																style={`bottom: 0; left: 0px; font-size: 0.85em; color: ${isDayInTheFuture ? '#393DB1' : '#2BAAA9'};`}>
																<Distance stores={this.props.stores} value={summaryWorkout.distanceKm} />
															</span>
														)}

													</td>
												);
											})}
											<td class='text-right pt-3 pb-3 position-relative border-right' style='font-size: 0.8em;'>
												<Distance stores={this.props.stores} value={totalWeek} />
												{totalWorkoutWeek > 0 && (
													<span class={`position-absolute`}
														style={`bottom: 0; left: 0px; font-size: 0.85em; color: ${isWeekInTheFuture ? '#393DB1' : '#2BAAA9'};`}>
														<Distance stores={this.props.stores} value={totalWorkoutWeek} />
													</span>
												)}
											</td>
											<td class='text-right pt-3 pb-3 text-muted border-right' style='font-size: 0.8em;'
												onClick={this.editWeek} data-week={weekObj.week} data-year={year} data-distance={summaryWeek.distance}
											>
												{summaryWeek.distance > 0 && <>
													<div class='rounded-pill w-100 px-1 text-white text-center'
														style={`background-color: ${mu.getPlanColor(summaryWeek, darkmode)};`}
													>
														<Distance stores={this.props.stores} value={summaryWeek.distance} />
													</div>
												</>}
											</td>
											{/* <td class='text-right text-muted pt-3'>{totalDistanceKmMonth}</td> */}
										</tr>
										{!hideWorkoutPlanTips && language === 'no' && tipsWeek && hasTipsWeek && <tr>
											<td colspan='10' class='pb-3'>
												<h5><span class='font-weight-lighter'>Uke {weekObj.week} {year}:</span>Tips fra Femundløpet</h5>

												{tipsWeek.totalDistance > 0 && <div>
													<span class='text-muted'><i class='fas fa-road' /> Distanse</span>: {tipsWeek.totalDistance} km
												</div>}
												{tipsWeek.distance > 0 && <div>
													<span class='text-muted'><i class='fas fa-road' /> Distanse pr økt</span>: {tipsWeek.distance}
												</div>}
												{tipsWeek.time && <div>
													<span class='text-muted'><i class='fas fa-clock' /> Tid pr økt</span>: {tipsWeek.time}
												</div>}
												{tipsWeek.totalWorkouts > 0 && <div>
													<span class='text-muted'><i class='fas fa-list-ol' /> Antall økter</span>: {tipsWeek.totalWorkouts}
												</div>}
												{tipsWeek.avgSpeed && <div>
													<span class='text-muted'><i class='fas fa-tachometer-alt' /> Gjennomsnittsfart</span>: {tipsWeek.avgSpeed}
												</div>}

												{tipsWeek.comments && <div>
													{tipsWeek.comments.map(c => <p>{c}</p>)}
												</div>}
											</td>
										</tr>}

									</>);
								})}
							</tbody>
							<tfoot>
								<tr>
									<td colspan='8' class='text-right pb-3'><Text id='workout.plan.totalkm'>Totalt km</Text></td>
									<td class='text-right position-relative pb-3'>
										<Distance stores={this.props.stores} value={totalDistanceKmMonth} />
										{totalWorkoutDistanceKmMonth > 0 && (
											<span class='position-absolute'
												style={`bottom: 0; left: 0px; font-size: 0.65em; color: ${isMonthInTheFuture ? '#393DB1' : '#2BAAA9'};`}
											>
												<Distance stores={this.props.stores} value={totalWorkoutDistanceKmMonth} />
											</span>
										)}
									</td>
									<td class='text-right text-muted'>
										<Distance stores={this.props.stores} value={totalDistanceKmMonthPlan} />
									</td>
								</tr>
								{!hideWorkoutPlanTips && language === 'no' && tipsMonth && hasTipsMonth && <tr>
									<td colspan='10' class='pb-3'>
										<h5><span class='font-weight-lighter'>{MONTHS[month - 1]} {year}:</span> Tips fra Femundløpet</h5>

										{tipsMonth.totalDistance > 0 && <div>
											<span class='text-muted'><i class='fas fa-road' /> Distanse</span>: {tipsMonth.totalDistance} km
										</div>}
										{tipsMonth.distance > 0 && <div>
											<span class='text-muted'><i class='fas fa-road' /> Distanse pr økt</span>: {tipsMonth.distance}
										</div>}
										{tipsMonth.time && <div>
											<span class='text-muted'><i class='fas fa-clock' /> Tid pr økt</span>: {tipsMonth.time}
										</div>}
										{tipsMonth.totalWorkouts > 0 && <div>
											<span class='text-muted'><i class='fas fa-list-ol' /> Antall økter</span>: {tipsMonth.totalWorkouts}
										</div>}
										{tipsMonth.avgSpeed && <div>
											<span class='text-muted'><i class='fas fa-tachometer-alt' /> Gjennomsnittsfart</span>: {tipsMonth.avgSpeed}
										</div>}

										{tipsMonth.comments && <div>
											{tipsMonth.comments.map(c => <p>{c}</p>)}
										</div>}
									</td>
								</tr>}
							</tfoot>
						</table>
					</div>
				</div>
			</>
		);
	}
}

export default Calendar;
